.carousel-block {
  position: relative;
}

.carousel-block__wrapper {
  overflow: hidden;
  position: relative;
}

.carousel-block__scroll-track {
  -webkit-overflow-scrolling: touch;
  overflow-x: auto;
  overflow-y: hidden;
  padding-bottom: 500px; // arbitrary height to hide scrollbar
  scroll-snap-points-x: repeat(100%);
  scroll-snap-type: mandatory;
  width: 100%;
}

.carousel-block__list {
  @include reset-list;
  margin: 0;
}

.carousel-block__slide {
  line-height: 0;
  margin: 0;

  &::before {
    display: none;
  }

  figure {
    margin: 0;
    padding: 0;
  }

  .carousel-block[data-init] & {
    float: left;
  }

  img {
    display: block;
    height: auto;
    width: 100%;
  }
}

.carousel-block__caption {
  [data-init] & {
    display: none;
  }
}

.carousel-block__caption-container {
  @include caption-long;
}

.carousel-block__previous,
.carousel-block__next {
  @include hide-text;
  appearance: none;
  background-color: rgba($color--black, 0.4);
  border: none;
  cursor: pointer;
  height: unit(8);
  margin-top: unit(-4);
  padding: 0;
  position: absolute;
  top: 50%;
  width: unit(3);

  &::before {
    @include pseudo;
    border-color: $color--white;
    border-style: solid;
    border-width: 3px 3px 0 0;
    height: unit(1);
    left: 50%;
    margin: unit(-0.5) 0 0 0;
    top: 50%;
    transition: all 0.2s $ease--snappy;
    width: unit(1);

    @include respond-to('medium-large') {
      border-color: $color--rose-of-sharon;
    }
  }

  @include respond-to('medium-large') {
    background-color: transparent;
  }
}

.carousel-block__previous {
  left: 0;

  &::before {
    margin-left: -4px;
    transform: rotate(-135deg);
  }

  @include hover {
    &::before {
      margin-left: -8px;
    }
  }

  @include respond-to('medium-large') {
    left: unit(-3);
  }
}

.carousel-block__next {
  right: 0;

  &::before {
    margin-left: -16px;
    transform: rotate(45deg);
  }

  @include hover {
    &::before {
      margin-left: -12px;
    }
  }

  @include respond-to('medium-large') {
    right: unit(-3);
  }
}
