/*
 * media queries
 * small:         30em
 * medium:        48.75em
 * medium-large:  61.25em
 * large:         75em
 * x-large:       87.5em
 */
.carousel-block {
  position: relative; }

.carousel-block__wrapper {
  overflow: hidden;
  position: relative; }

.carousel-block__scroll-track {
  -webkit-overflow-scrolling: touch;
  overflow-x: auto;
  overflow-y: hidden;
  padding-bottom: 500px;
  scroll-snap-points-x: repeat(100%);
  scroll-snap-type: mandatory;
  width: 100%; }

.carousel-block__list {
  list-style: none;
  padding: 0;
  margin: 0; }

.carousel-block__slide {
  line-height: 0;
  margin: 0; }
  .carousel-block__slide::before {
    display: none; }
  .carousel-block__slide figure {
    margin: 0;
    padding: 0; }
  .carousel-block[data-init] .carousel-block__slide {
    float: left; }
  .carousel-block__slide img {
    display: block;
    height: auto;
    width: 100%; }

[data-init] .carousel-block__caption {
  display: none; }

.carousel-block__caption-container {
  border-bottom: 1px solid #eaeae9;
  font-size: 1.2rem;
  font-weight: 500;
  padding: 1rem 2rem;
  text-align: center; }

.carousel-block__previous,
.carousel-block__next {
  overflow: hidden;
  text-indent: 100%;
  white-space: nowrap;
  appearance: none;
  background-color: rgba(0, 0, 0, 0.4);
  border: none;
  cursor: pointer;
  height: 16rem;
  margin-top: -8rem;
  padding: 0;
  position: absolute;
  top: 50%;
  width: 6rem; }
  .carousel-block__previous::before,
  .carousel-block__next::before {
    content: "";
    display: block;
    position: absolute;
    border-color: #fff;
    border-style: solid;
    border-width: 3px 3px 0 0;
    height: 2rem;
    left: 50%;
    margin: -1rem 0 0 0;
    top: 50%;
    transition: all 0.2s cubic-bezier(0.75, 0, 0.125, 1);
    width: 2rem; }
    @media (min-width: 61.25em) {
      .carousel-block__previous::before,
      .carousel-block__next::before {
        border-color: #c25700; } }
  @media (min-width: 61.25em) {
    .carousel-block__previous,
    .carousel-block__next {
      background-color: transparent; } }

.carousel-block__previous {
  left: 0; }
  .carousel-block__previous::before {
    margin-left: -4px;
    transform: rotate(-135deg); }
  .carousel-block__previous:focus::before, .carousel-block__previous:hover::before {
    margin-left: -8px; }
  @media (min-width: 61.25em) {
    .carousel-block__previous {
      left: -6rem; } }

.carousel-block__next {
  right: 0; }
  .carousel-block__next::before {
    margin-left: -16px;
    transform: rotate(45deg); }
  .carousel-block__next:focus::before, .carousel-block__next:hover::before {
    margin-left: -12px; }
  @media (min-width: 61.25em) {
    .carousel-block__next {
      right: -6rem; } }
